import { styled, css, injectGlobal } from '../../';
//import { injectGlobal } from 'styled-components';




const root = injectGlobal`

@import url('https://fonts.googleapis.com/css?family=Roboto:100,500|Source-Sans-Pro');

:root {
  --font-family: 'Source-Sans-Pro', Helvetica, sans-serif;
  --font-family-fixed: 'Courier New', monospace;
  --font-weight: 300;
  --font-weight-bold: 600;
  --letter-spacing: 0.025em;
  --letter-spacing-alt: 0.25em;
  --color-bg:        rgba(036, 041, 067, 1.0);
  --color-bg-alt:    rgba(042, 047, 074, 1.0);
  --color-fg:        rgba(255, 255, 255, 1.0);
  --color-fg-bold:   rgba(255, 255, 255, 1.0);
  --color-fg-light:  rgba(244, 244, 255, 0.2);
  --color-border:    rgba(212, 212, 255, 0.1);
  --color-border-bg: rgba(212, 212, 255, 0.035);
  --color-highlight: rgba(155, 241, 255, 1.0);
  --color-accent1:   rgba(111, 195, 223, 1.0);
  --color-accent2:   rgba(141, 130, 196, 1.0);
  --color-accent3:   rgba(236, 141, 129, 1.0);
  --color-accent4:   rgba(231, 183, 136, 1.0);
  --color-accent5:   rgba(142, 169, 232, 1.0);
  --color-accent6:   rgba(135, 197, 164, 1.0);
}

a, abbr, acronym, address, applet, article, aside, audio, b, big, blockquote, body, canvas, caption, center, cite, code, dd, del, details, dfn, div, dl, dt, em, embed, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html, i, iframe, img, ins, kbd, label, legend, li, mark, menu, nav, object, ol, output, p, pre, q, ruby, s, samp, section, small, span, strike, strong, sub, summary, sup, table, tbody, td, tfoot, th, thead, time, tr, tt, u, ul, var, video {
  margin: 0;
  padding: 0;
  border: 0;
}

body {
  background-color: var(--color-bg);
  color: var(--color-fg);
  font-family: var(--font-family);
  font-size: 3vmin;
}

a:link {color: inherit;}
a:visited {color: inherit;}
a:hover {color: var(--color-highlight);}
a:hover > strong {background-color: var(--color-highlight);}
a:active {color: inherit;}
a span svg > * {
  transition: 200ms;
  transition-timing-function: ease-in-out;
  -webkit-transition: 200ms;
  -webkit-transition-timing-function: ease-in-out;
}

a:hover span svg > * {
  fill: var(--color-highlight);
}

a {
  text-decoration: none;
  transition: color .2s ease-in-out,
  border-bottom-color .2s ease-in-out;
}

`
